$('#close-ads').click(function() {
    $('.bigbanner-ad__content--large').hide()
    $('.bigbanner-ad__content--small').show();
});

$('#open-ads').click(function() {
    $('.bigbanner-ad__content--small').hide()
    $('.bigbanner-ad__content--large').show();
});

setTimeout(function(){jQuery('#close-ads').trigger('click');},15000);